export class ComponentFirst extends Component {
    constructor(props) {
        super(props);

        this.state = {
            isLoaded: false,
            content: {},
        };
    }

    componentDidMount() {
        const name = this.getName();
        const content = getContent(name);

        this.setState({
            isLoaded: true, 
            content,
        });
    }

    componentDidUpdate(prevProps) {
        const oldName = prevProps.match.params.name;
        const newName = this.props.match.params.name;
        if (oldName === newName) { return;}
        if (oldName !== newName) {
            const name = this.getName();
            const content = getContent(name);

            this.setState({
                content,
            });
        }
    }

    getName() {
        return this.props.match.params.name;
    }

    render() {
        if (!this.state.isLoaded) {
            return <span>Loading...</span>;
        }

        const { title, text } = this.state.content;

        return (
            <div className="content">
                <ComponentHeader/>
                <Menu/>
                <div className="content__container">
                    <h3 className="content__title">{title}</h3>
                    <div className="content__text-container">
                        <p className="content__text">{text}</p>
                    </div>
                </div>
            </div>
        );
    }
}